.login-container {
  position: relative;
  z-index: 1;
  background: #FFFFFF;
  max-width: 360px;
  margin: 0 auto 100px;
  margin-top: 10%;
  padding: 45px;
  text-align: center;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}

.input {
  font-family: "Roboto", sans-serif;
  outline: 0;
  background: #f2f2f2;
  width: 100%;
  border: 0;
  margin: 0 0 15px;
  padding: 15px;
  box-sizing: border-box;
  font-size: 14px;
}

#submit-button {
  font-family: "Roboto", sans-serif;
  text-transform: uppercase;
  outline: 0;
  background: #4CAF50;
  width: 100%;
  border: 0;
  padding: 15px;
  color: #FFFFFF;
  font-size: 14px;
  -webkit-transition: all 0.3 ease;
  transition: all 0.3 ease;
  cursor: pointer;
}

#submit-button:hover,
#submit-button:active,
#submit-button:focus {
  background: #43A047;
}

.logo-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

#wallet
{
  max-width: 150%;
  margin-bottom: 30px;
  margin-left: -5px;
}

#trybe {
  max-width: 290%;
}

.header {
  display: flex;
  align-items: baseline;
  justify-content: space-around;
  background-color: #1e555c;
  color: black;
  height: 100px;
}

.link {
  text-decoration: none;
  font-size: 30px;
  color: black;
}

.link:hover {
  text-decoration: none;
  color: #d4d6b9;
}
